import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Grommet, Box, Heading, Paragraph, Button } from 'grommet';
import HeaderBar from './HeaderBar'
import {withCookies, Cookies} from 'react-cookie';
import {instanceOf} from 'prop-types';
import {Youtube} from 'grommet-icons';

const theme = {
    global: {
      colors: {
        brand: '#3F51B5'
      },
      font: {
        family: "'-apple-system', 'BlinkMacSystemFont', 'Roboto'",
        size: '16px',
        height: '20px',
      },
    },
  };

  

class Quiz extends Component {
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };

    constructor(props){
        super(props);

        const { cookies } = props;

        this.state = {
            watched: cookies.get('watched') || false,
            clickNotEnd: false
        };
    }
    render() {
        return (
            <Grommet full theme={theme}>
                
                <Box fill>
                <HeaderBar />
                {(this.state.watched &&
                <Box direction='row' flex overflow={{ horizontal: 'hidden' }}>
                    <Box flex align='center' justify='center' margin="medium">
                    <Heading levle="2" alignSelf="start" margin="large">Quiz</Heading>
                    <Box fill={true} alignSelf="start" margin="large">
                        <Heading level="3">Quiz 1</Heading>
                        <Paragraph fill={true}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Paragraph>
                    </Box>
                    <Box fill={true} alignSelf="start" margin="large">
                        <Heading level="3">Quiz 2</Heading>
                        <Paragraph fill={true}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Paragraph>
                    </Box>
                    <Box fill={true} alignSelf="start" margin="large">
                        <Heading level="3">Quiz 3</Heading>
                        <Paragraph fill={true}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</Paragraph>
                    </Box>
                    </Box>
                </Box>
                )}
                {(!this.state.watched &&
                <Box direction='row' flex>
                    <Box flex align ='center' justify='center' margin='small'>
                   <Heading level="3" >먼저 교육 영상을 시청해주세요</Heading>
                   <Heading level="4" >교육영상을 시청하신 후에 확인 퀴즈 참여가 가능합니다.</Heading>
                   <Button icon={<Youtube/>} label="Video" margin="medium" onClick={() => window.location.href = '/video'}/>
                   </Box>
                </Box>
                )}
                </Box>
            </Grommet>
        );
    }
}

export default withCookies(withRouter(Quiz))